.form-field {
  box-sizing: border-box;
  display: block;
  width: 100%;
  border: none;
  border-bottom: 1px solid #fff;
  background-color: $colorDarkGrey;
  color: #fff;
  padding: 0 0 17px;

  &:focus {
    background-color: $colorDarkGrey;
    outline: none;
    color: #fff;
  }

  &:first-child {
    padding-bottom: 16px;
    border-bottom: 1px solid #fff;
  }

  &::placeholder {
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    font-size: 15px;
    line-height: 22px;
    color: #fff;
    opacity: 0.7;
  }

  &:focus::placeholder {
    opacity: 1;
  }

  @include tablet {
    width: 370px;
  }

  @include smallDesktop {
    width: 100%;
  }

  &--textarea {
    height: 50px;
    resize: none;
    font-family: inherit;
  }

  &:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 1000px $colorDarkGrey inset !important;
    -webkit-text-fill-color: #fff !important;
  }
}
